/* General Styling*/

body {
    font-family: "Montserrat", sans-serif;
}

a {
    color: #06c;
}

img {
    max-width: 100%;
}

.white {
    color: #fff;
}

.black {
    color: #1d1d1f;
}

.grey {
    color: #86868b;
}

.top-50 {
    margin-top: 50px;
}

.top-100 {
    margin-top: 100px;
}

.bottom-100 {
    margin-bottom: 100px;
}

.bottom-50 {
    margin-bottom: 50px;
}

.top-150 {
    margin-top: 150px;
}


/* Header Style */

.nav-wrapper {
    background: rgba(0, 0, 0, 0.8);
    font-size: 0.85em;
    font-weight: 200;
}

.nav-wrapper ul li a {
    color: #fff;
}

.navbar-toggler {
    color: #fff;
}

.nav-item {
    text-align: left !important;
    padding: 4px 0;
    border-bottom: 1px solid #4b4b4b;
}


/* Footer Styling */

.footer-wrapper {
    background-color: #f5f5f7;
    color: #86868b;
    font-size: 0.7em;
}

.footer-wrapper ul {
    list-style: none;
    padding-bottom: 10px;
    padding-inline-start: 0px;
}

.footer-wrapper ul a {
    color: #515154;
}

.my-apple-wrapper {
    padding-bottom: 15px;
    margin-top: 10px;
}

.copyright {
    margin-top: 15px;
}

.footer-country {
    margin-top: 8px;
}

.flag-wrapper {
    display: inline-block;
    vertical-align: middle;
    padding-right: 5px;
}

.footer-country-name {
    display: inline-block;
    vertical-align: middle;
}

.footer-links-terms ul {
    display: flex;
}

.footer-links-terms ul li {
    padding: 0 10px;
    border-right: 1px solid #d2d2d7;
}

.footer-links-terms ul li:first-child {
    padding-left: 0;
}

.footer-links-terms ul li:last-child {
    border-right: 0;
}

.footer-links-wrapper h3 {
    font-size: 12px;
    font-weight: 600;
    color: #1d1d1f;
}

.footer-wrapper ul li {
    padding: 4px 0;
}

.upper-text-container {
    border-bottom: 1px solid #d2d2d7;
    padding: 17px 0 10px;
    margin-bottom: 20px;
}

.upper-text-container a {
    color: #515154;
}


/*Sixth Section*/

.sixth-heghlight-wrapper .left-side-wrapper .left-side-container {
    background-image: url("../images/home/spyder.jpg");
    background-color: #000;
    background-position: bottom;
    padding-top: 35px;
    margin: 0 0 10px 0;
    background-size: cover;
}

.sixth-heghlight-wrapper .right-side-wrapper .right-side-container {
    background-image: url("../images/home/apple-card-monthly.jpg");
    background-color: #fbfbfd;
    background-position: bottom;
    padding-top: 35px;
    margin: 0 0 10px 0;
    background-size: auto;
}

.left-side-container {
    min-height: 515px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
}

.right-side-container {
    min-height: 515px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
}

.left-side-wrapper {
    padding-right: 0px;
    padding-left: 0px;
}

.right-side-wrapper {
    padding-right: 0px;
    padding-left: 0px;
}

.sixth-heghlight-wrapper .links-wrapper {
    max-width: 300px;
    margin: 0 auto;
}

.links-wrapper ul {
    list-style: none;
}

.product-title {
    font-weight: 700;
    font-size: 56px;
}

.brief-description {
    font-size: 2em;
}

.sixth-heghlight-wrapper .description-wraper {
    max-width: 250px;
    margin: 7px auto;
}

.description-wraper.white {
    color: white;
}

.links-wrapper {
    padding: 15px 0;
}

.links-wrapper ul li {
    display: inline-block;
    padding-right: 20px;
}

.links-wrapper ul li a {
    color: #06c;
    text-decoration: none;
    font-size: 21px;
}

.links-wrapper ul li a:hover {
    color: #000;
}

.links-wrapper ul li a:after {
    font-family: "FontAwesome";
    font-weight: 900;
    content: "\f105";
    padding-left: 10px;
}

.title-wraper {
    font-weight: 600;
    font-size: 30px;
    line-height: 1.15em;
    letter-spacing: -0.04em;
}

.title-wraper.bold {
    font-weight: 600;
    font-size: 32px;
    line-height: 1.15em;
    letter-spacing: -0.04em;
}

.right-side-wrapper .title-wraper {
    max-width: 280px;
    margin: 0 auto;
}

.description-wrapper {
    font-size: 26px;
    padding: 10px 0;
}


/*Fifth Section*/

.fifth-heghlight-wrapper .left-side-wrapper .left-side-container {
    background-image: url("../images/home/actors.jpg");
    background-color: #000;
    padding-top: 35px;
    margin: 0 0 10px 0;
    background-size: cover;
}

.fifth-heghlight-wrapper .right-side-wrapper .right-side-container {
    background-image: url("../images/home/watch.jpg");
    background-color: #fbfbfd;
    background-position: bottom;
    padding-top: 35px;
    margin: 0 0 10px 0;
    background-size: auto;
}

.fifth-heghlight-wrapper .description-wraper {
    max-width: 450px;
    margin: 7px auto;
}

.watch-more-wrapper a {
    text-decoration: none;
    font-size: 0.85em;
    color: #fff;
}

.watch-more-wrapper a:after {
    font-family: "FontAwesome";
    font-weight: 900;
    content: "\f144";
    padding-left: 15px;
}

.tvshow-logo-wraper {
    padding: 340px 0 0px;
}


/* Fourth section */

.fourth-heghlight-wrapper .left-side-wrapper .left-side-container {
    background-image: url("../images/home/iphone11-colored.jpg");
    background-color: #000;
    padding-top: 35px;
    margin: 0 0 10px 0;
    background-size: cover;
}

.fourth-heghlight-wrapper .right-side-wrapper .right-side-container {
    background-image: url("../images/home/apple-blue-logo.jpg");
    background-color: #165ca9;
    background-position: bottom;
    padding-top: 35px;
    margin: 0 0 10px 0;
    background-size: auto;
}

.links-wrapper.white li a {
    color: #fff;
}

.fourth-heghlight-wrapper .right-side-wrapper .title-wraper {
    max-width: 380px;
}


/*Third section*/

.third-hightlight-wrapper {
    background-image: url("../images/home/iphone11-pro-bg.jpg");
    height: 580px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    padding: 65px 20px;
    color: #fff;
}


/*Second section*/

.second-hightlight-wrapper {
    background-image: url("../images/home/macbookair-new-sm.jpg");
    height: 580px;
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: initial;
    background-color: #fbfbfd;
    text-align: center;
    padding: 65px 20px;
    color: #fff;
    margin-bottom: 10px;
}


/*First Section*/

.first-hightlight-wrapper {
    background-image: url("../images/home/ipodPronew-sm.jpg");
    height: 580px;
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: initial;
    background-color: #fbfbfd;
    text-align: center;
    padding: 65px 20px;
    color: #fff;
    margin-bottom: 10px;
}

.ipod-caption {
    color: #86868b;
    font-size: 12px;
    line-height: 1.33337;
    font-weight: 400;
    letter-spacing: -0.01em;
    padding-top: 310px;
}


/* Alert section */

.alert-section {
    text-align: center;
    padding: 20px;
}

.alert-title {
    font-weight: 600;
    color: #1d1d1f;
    padding: 10px;
}

.alert-text {
    max-width: 90%;
    margin: 0 auto;
}


/*Media Query*/


/* Footer */

@media (max-width: 768px) {
    .footer-links-wrapper ul {
        display: none;
    }
    .footer-links-wrapper h3 {
        padding: 10px 0;
        border-bottom: 1px solid #ccc;
    }
    .footer-links-wrapper h3:after {
        font-family: "FontAwesome";
        content: "\f067";
        padding-left: 10px;
        position: absolute;
        right: 25px;
    }
}

@media (min-width: 768px) {
    /*Nav bar*/
    .nav-item {
        text-align: center !important;
        padding: 0;
        border-bottom: 0px;
    }
    /*Footer styling*/
    .my-apple-wrapper {
        border-bottom: 1px solid #d2d2d7;
    }
    /*Sixth section*/
    .sixth-heghlight-wrapper .left-side-wrapper .left-side-container {
        margin: 5px 5px 10px 10px;
    }
    .sixth-heghlight-wrapper .right-side-wrapper .right-side-container {
        margin: 5px 10px 10px 5px;
    }
    .sixth-heghlight-wrapper .links-wrapper {
        max-width: 500px;
    }
    /*Fifth section*/
    .fifth-heghlight-wrapper .left-side-wrapper .left-side-container {
        margin: 5px 5px 5px 10px;
    }
    .fifth-heghlight-wrapper .right-side-wrapper .right-side-container {
        margin: 5px 10px 5px 5px;
    }
    /*fourth section*/
    .fourth-heghlight-wrapper .left-side-wrapper .left-side-container {
        margin: 10px 5px 5px 10px;
    }
    .fourth-heghlight-wrapper .right-side-wrapper .right-side-container {
        margin: 10px 10px 5px 5px;
    }
    .title-wraper.bold {
        font-size: 56px;
    }
    /* Second section */
    .second-hightlight-wrapper {
        min-height: 692px;
        background-image: url("../images/home/macbookair-new.jpg");
    }
    .new-alert {
        color: #bf4801;
        font-size: 18px;
    }
    /* First section */
    .first-hightlight-wrapper {
        background-image: url("../images/home/ipodPronew.jpg");
        min-height: 692px;
    }
    .ipod-caption {
        padding-top: 415px;
    }
    /*Alert section*/
    .alert-text {
        max-width: 85%;
        margin: 0 auto;
    }
}

@media (min-width: 992px) {
    /*Footer styling*/
    .copyright {
        margin-top: 10px;
    }
    .footer-country {
        margin-top: 10px;
    }
    .footer-links-terms {
        margin-top: 10px;
    }
}


/* Video Area */

.youtubeVideosWrapper {
    background: #fff;
    padding: 50px;
}

.singleVideoWrapper {
    background: #f3f2f2;
    box-shadow: 4px 0px 13px rgba(158, 158, 158, 0.12);
    margin-bottom: 30px;
    max-height: 393px;
    overflow: hidden;
}

.videoInfoWrapper {
    padding: 10px 20px;
    text-align: left;
}

.videoDesc {
    font-size: 0.6em;
    color: #676767;
}

.videoTitle {
    font-weight: 700;
    line-height: 1.2em;
}

.videoTitle a {
    color: #000;
}

.video-title-wrapper {
    padding: 50px 0;
}

.active {
    font-weight: bold;
    color: #0071e3 !important;
}